* {
  box-sizing: border-box;
  outline: none;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

body {
  min-height: 100vh;
  background: linear-gradient(to right, #fe4d21, #fd943c);
  justify-content: center;
  display: flex;
}

.container {
  height: 100%;
  background: linear-gradient(to right, #5af5d0, #4bcff0);
  border-radius: 40px;
  margin-top: 10vh;
}

.form-container {
  padding: 30px 140px;
}

h1 {
  text-transform: uppercase;
  color: #5e423d;
  text-shadow: -2px 2px 1px #fe4d21;
  letter-spacing: 8px;
  margin-bottom: 40px;
  font-size: 3.3rem;
}

form {
  text-align: center;
}

input {
  color: #b0ecc2;
  background: linear-gradient(to right, #fe4d21, #fd943c);
  border: 2px solid red;
  border-radius: 50px;
  margin: 0 auto 20px;
  padding: 10px 0 10px 15px;
  font-size: 1.3rem;
  box-shadow: 3px 3px 10px 1px #0006;
}

input:focus {
  border: 2px solid #ff0;
}

input::placeholder {
  color: #b0ecc2;
}

ul {
  width: 80%;
  margin: 0 0 40px 80px;
}

li {
  cursor: pointer;
  margin-bottom: 15px;
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
  font-size: 1.2rem;
  list-style-type: circle;
  animation: 1s forwards anim;
}

li::marker {
  color: #000;
}

ul li:after {
  opacity: 0;
}

ul li:hover:after {
  content: " (terminé)";
  color: #fe4d21;
  font-size: 1rem;
  font-style: italic;
  animation: .5s forwards anim;
}

@keyframes anim {
  to {
    opacity: 1;
  }
}

.checked {
  text-decoration: line-through;
  list-style-image: url("icons8-checked-12.a5c17e15.png");
}

li.checked:after {
  opacity: 0;
}

li.checked:hover:after {
  content: " (supprimer)";
  color: #fe4d21;
  font-size: 1rem;
  font-style: italic;
  animation: .5s forwards anim;
}

@media screen and (max-width: 1024px) {
  .form-container {
    padding: 25px 120px;
  }

  h1 {
    font-size: 3rem;
  }
}

@media screen and (max-width: 768px) {
  .form-container {
    padding: 20px 60px;
  }

  h1 {
    font-size: 2.7rem;
  }

  ul {
    margin: 0 0 40px 60px;
  }
}

@media screen and (max-width: 425px) {
  .form-container {
    padding: 20px 10px;
  }

  h1 {
    text-align: center;
    font-size: 2.2rem;
  }

  ul {
    margin: 0 0 40px 60px;
  }

  input {
    width: 200px;
    margin-bottom: 20px;
    padding: 5px 0 5px 8px;
    font-size: 1.1rem;
  }
}

@media screen and (max-width: 375px) {
  .form-container {
    padding: 10px 5px;
  }

  h1 {
    font-size: 2rem;
  }

  ul {
    margin: 0 0 40px 50px;
  }

  input {
    width: 150px;
    font-size: 1rem;
  }
}

